import React, { useState } from "react";

// Components
import ProjectComponent from "../components/ProjectComponent";
import ProjectModal from "../components/ProjectModal";

// Animation
import { motion } from "framer-motion";

// Styles
import styled from "styled-components";

// Project Data
import projectData from "../projectData";

const Projects = () => {
    // States
    const [projects, setProjects] = useState(projectData());
    const allProjects = projectData();
    const [filter, setFilter] = useState("all");
    const [markerPosition, setMarkerPosition] = useState(0);
    const [modalProject, setModalProject] = useState({});
    const [modalToggle, setModalToggle] = useState(false);

    // Handlers
    const filterHandler = (tag) => {
        setMarkerPosition(0);
        setFilter(tag);

        setProjects(
            allProjects.filter((eachProject) => eachProject.tags.includes(tag))
        );
    };

    return (
        <StyledProjects>
            <Filter>
                <FilterTags
                    onClick={() => filterHandler("all")}
                    className={filter === "all" ? "filter-active" : ""}
                >
                    <p>all</p>
                    <Highlight></Highlight>
                </FilterTags>

                <FilterTags
                    onClick={() => filterHandler("javascript")}
                    className={filter === "javascript" ? "filter-active" : ""}
                >
                    <p>javascript</p>
                    <Highlight></Highlight>
                </FilterTags>

                <FilterTags
                    onClick={() => filterHandler("python")}
                    className={filter === "python" ? "filter-active" : ""}
                >
                    <p>python</p>
                    <Highlight></Highlight>
                </FilterTags>

                <FilterTags
                    onClick={() => filterHandler("others")}
                    className={filter === "others" ? "filter-active" : ""}
                >
                    <p>others</p>
                    <Highlight></Highlight>
                </FilterTags>
            </Filter>

            <ProjectsList>
                <div
                    className="marker"
                    style={{
                        transform: `translateY(${
                            (markerPosition * 100) / projects.length
                        }%)`,
                    }}
                >
                    <h2>0{markerPosition + 1}</h2>
                    <h2>/ 0{projects.length}</h2>
                </div>

                <div className="projects-container">
                    {projects.map((project) => (
                        <ProjectComponent
                            setModalProject={setModalProject}
                            setModalToggle={setModalToggle}
                            markerPosition={markerPosition}
                            setMarkerPosition={setMarkerPosition}
                            projects={projects}
                            project={project}
                            key={project.id}
                        />
                    ))}
                </div>
            </ProjectsList>

            <ProjectModal
                modalProject={modalProject}
                modalToggle={modalToggle}
                setModalToggle={setModalToggle}
            />
        </StyledProjects>
    );
};

const StyledProjects = styled(motion.div)`
    padding: 0rem 13rem;
    text-align: right;
    min-height: 85vh;
    display: flex;
    flex-direction: column;
    align-items: flex-end;

    h2 {
        font-size: 5rem;
        line-height: 10rem;
        font-family: "Lato", sans-serif;
    }
`;

const Filter = styled(motion.div)`
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 60%;
    margin-bottom: 3rem;
    position: relative;
    z-index: 50;

    .filter-active p {
        color: #eff2f5;
    }

    .filter-active div {
        transform: translateY(0%);
    }
`;

const FilterTags = styled(motion.div)`
    font-family: "Lora", serif;
    font-size: 1.5rem;
    padding: 0.7rem 1.2rem;
    position: relative;
    overflow: hidden;
    cursor: pointer;

    &:hover div {
        transform: translateY(0%);
    }

    &:hover p {
        color: #eff2f5;
    }
`;

const Highlight = styled(motion.div)`
    position: absolute;
    width: 120%;
    height: 100%;
    background: #ffb0a6;
    box-sizing: content-box;
    top: 0;
    left: 0;
    transition: transform 0.5s ease;
    z-index: -1;
    transform: translateY(96.5%);
`;

const ProjectsList = styled(motion.div)`
    display: flex;
    width: 60%;
    justify-content: space-between;
    overflow: hidden;

    .marker {
        width: 7.5rem;
        transition: all 0.5s ease;
        display: flex;
        justify-content: space-between;
        color: #bdc0c3;

        h2 {
            line-height: 10rem;
            font-size: 2rem;
            font-weight: lighter;
        }
    }
`;

export default Projects;
