import React from "react";

// Animation
import { motion } from "framer-motion";

// Styles
import styled from "styled-components";

const ProjectComponent = ({
    project,
    projects,
    setMarkerPosition,
    setModalProject,
    setModalToggle,
}) => {
    // Handler
    const markerHandler = () => {
        setMarkerPosition(projects.indexOf(project));
    };

    const modalPropHandler = (data) => {
        setModalProject(data);
        setModalToggle(true);
        document.querySelector(".scroll").scrollTo({ top: 0 });
        document.querySelector(".scroll").style.overflow = "auto";
        document.body.style.overflow = "hidden";
    };

    return (
        <StyledProjectComponent>
            <motion.h2
                onMouseEnter={markerHandler}
                onClick={() => modalPropHandler(project)}
            >
                {project.name}
            </motion.h2>
        </StyledProjectComponent>
    );
};

const StyledProjectComponent = styled(motion.div)``;

export default ProjectComponent;
