import React from "react";
import { Link } from "react-router-dom";

// Animation
import { motion } from "framer-motion";
import {
    navEnter,
    navLeave,
    burgerAnim,
    navElementsEnter,
    navElementsExit,
    crossAnim,
} from "../animations";

// Styles
import styled from "styled-components";

const Nav = ({ setNavStatus, navStatus, theme, pathname }) => {
    // Handlers
    const navOpenHandler = () => {
        setNavStatus(!navStatus);
    };

    return (
        <StyledNav>
            <Burger onClick={navOpenHandler}>
                <BurgerLines
                    animate={navStatus ? burgerAnim.show : burgerAnim.hidden}
                    initial={false}
                >
                    <Line1 theme={theme} />
                    <Line2 theme={theme} />
                    <Line3 theme={theme} />
                    <Line4 theme={theme} />
                </BurgerLines>

                <CrossLines
                    theme={theme}
                    className="cross-lines"
                    animate={navStatus ? crossAnim.show : crossAnim.hidden}
                    initial={false}
                />
            </Burger>

            <p>J.L</p>

            <NavBar
                variants={navStatus ? navEnter : navLeave}
                initial={false}
                animate="show"
            >
                <NavElements theme={theme}>
                    <LinkContainer
                        variants={
                            navStatus ? navElementsEnter : navElementsExit
                        }
                    >
                        <Link
                            className={pathname === "/" ? "test" : ""}
                            to="/"
                            replace
                        >
                            <motion.div
                                transition={{
                                    duration: 0.3,
                                    ease: "easeInOut",
                                }}
                                initial={{ color: theme.primary }}
                                animate={{
                                    color:
                                        pathname === "/"
                                            ? theme.secondary
                                            : theme.primary,
                                }}
                            >
                                HOME
                            </motion.div>
                            <Highlight
                                theme={theme}
                                className="highlight"
                                transition={{
                                    duration: 0.1,
                                    ease: "easeInOut",
                                }}
                                initial={{ y: "100%" }}
                                animate={{
                                    y: pathname === "/" ? 0 : "100%",
                                }}
                            ></Highlight>
                        </Link>
                    </LinkContainer>

                    <LinkContainer
                        variants={
                            navStatus ? navElementsEnter : navElementsExit
                        }
                    >
                        <Link
                            className={pathname === "/aboutme" ? "test" : ""}
                            to="/aboutme"
                            replace
                        >
                            <motion.div
                                transition={{
                                    duration: 0.3,
                                    ease: "easeInOut",
                                }}
                                initial={{ color: theme.primary }}
                                animate={{
                                    color:
                                        pathname === "/aboutme"
                                            ? theme.secondary
                                            : theme.primary,
                                }}
                            >
                                ABOUT ME
                            </motion.div>
                            <Highlight
                                theme={theme}
                                className="highlight"
                                transition={{
                                    duration: 0.1,
                                    ease: "easeInOut",
                                }}
                                initial={{ y: "100%" }}
                                animate={{
                                    y: pathname === "/aboutme" ? 0 : "100%",
                                }}
                            ></Highlight>
                        </Link>
                    </LinkContainer>

                    <LinkContainer
                        variants={
                            navStatus ? navElementsEnter : navElementsExit
                        }
                    >
                        <Link
                            className={pathname === "/projects" ? "test" : ""}
                            to="/projects"
                            replace
                        >
                            <motion.div
                                transition={{
                                    duration: 0.3,
                                    ease: "easeInOut",
                                }}
                                initial={{ color: theme.primary }}
                                animate={{
                                    color:
                                        pathname === "/projects"
                                            ? theme.secondary
                                            : theme.primary,
                                }}
                            >
                                PROJECTS
                            </motion.div>
                            <Highlight
                                theme={theme}
                                className="highlight"
                                transition={{
                                    duration: 0.1,
                                    ease: "easeInOut",
                                }}
                                initial={{ y: "100%" }}
                                animate={{
                                    y: pathname === "/projects" ? 0 : "100%",
                                }}
                            ></Highlight>
                        </Link>
                    </LinkContainer>

                    <LinkContainer
                        variants={
                            navStatus ? navElementsEnter : navElementsExit
                        }
                    >
                        <a
                            href="https://github.com/jonathanlin505/"
                            target="_blank"
                            rel="noreferrer"
                        >
                            GITHUB
                            <Highlight
                                theme={theme}
                                className="highlight"
                            ></Highlight>
                        </a>
                    </LinkContainer>
                </NavElements>
            </NavBar>
        </StyledNav>
    );
};

const Highlight = styled(motion.div)`
    position: absolute;
    width: 100%;
    height: 100%;
    background: ${(props) => props.theme.primary};
    box-sizing: content-box;
    top: 0;
    left: 0;
    transition: all 0.5s ease;
    transform: translateY(100%);
    z-index: -1;
`;

const NavBar = styled(motion.div)`
    position: absolute;
    top: 8rem;
    left: 0;
    padding: 0rem 0rem 0rem 4.3rem;
    z-index: 15;
    transform: translateX(-100%);
`;

const NavElements = styled(motion.div)`
    display: flex;
    flex-direction: column;

    a {
        font-size: 1.5rem;
        font-family: "Lato", serif;
        color: ${(props) => props.theme.primary};
        padding: 1rem;
        position: relative;
        display: inline-block;
        transition: 0.5s all ease;
        overflow: hidden;
    }
`;

const LinkContainer = styled(motion.div)`
    white-space: nowrap;

    &:hover a {
        transform: translateX(1rem);
    }

    .test {
        transform: translateX(1rem);
    }
`;

const StyledNav = styled(motion.nav)`
    padding: 0rem 5rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-height: 15vh;
    position: sticky;
    font-size: 1.2rem;
    top: 0;
    left: 0;
    z-index: 13;
`;

const Burger = styled(motion.div)`
    cursor: pointer;
    position: relative;
    z-index: 17;

    &:hover div div {
        width: 100%;
        background: ${(props) => props.theme.tertiary};
    }

    &:hover .cross-lines:after,
    &:hover .cross-lines:before {
        border-left: 0.15rem solid ${(props) => props.theme.tertiary};
    }
`;

const BurgerLines = styled(motion.div)`
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    flex-direction: column;
    height: 2rem;
    width: 2.3rem;
    position: relative;
`;

const CrossLines = styled(motion.div)`
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;

    &:after {
        content: "";
        height: 120%;
        border-left: 0.15rem solid ${(props) => props.theme.primary};
        border-radius: 5rem;
        position: absolute;
        transform: rotate(45deg);
        left: 1.1rem;
        bottom: -0.25rem;
        transition: all 0.5s ease;
    }

    &:before {
        content: "";
        height: 120%;
        border-left: 0.15rem solid ${(props) => props.theme.primary};
        border-radius: 5rem;
        position: absolute;
        transform: rotate(-45deg);
        left: 1.1rem;
        bottom: -0.25rem;
        transition: all 0.5s ease;
    }
`;

const Line1 = styled(motion.div)`
    width: 100%;
    height: 6%;
    background: ${(props) => props.theme.primary};
    border-radius: 5rem;
    transition: all 0.5s ease;
`;

const Line2 = styled(Line1)`
    width: 60%;
`;

const Line3 = styled(Line1)``;

const Line4 = styled(Line1)`
    width: 80%;
`;

export default Nav;
