import React, { useState, useEffect } from "react";

// Pages
import Home from "./pages/Home";
import AboutMe from "./pages/AboutMe";
import Projects from "./pages/Projects";

// Components
import Nav from "./components/Nav";

// Style
import { ThemeProvider } from "styled-components";
import GlobalStyle1 from "./GlobalStyle1";

// Router
import { Switch, Route, useLocation } from "react-router-dom";

function App() {
    const { pathname } = useLocation();

    // UseState
    const [navStatus, setNavStatus] = useState(false);
    const [theme, setTheme] = useState({
        primary: "#CEAE8C",
        secondary: "#2A313B",
        tertiary: "#dfd5ca",
        initial: true,
    });

    // Effects
    useEffect(() => {
        if (pathname === "/projects") {
            setTheme({
                primary: "#FFB0A6",
                secondary: "#EFF2F5",
                tertiary: "#bdc0c3",
                initial: false,
            });
        } else if (pathname === "/aboutme") {
            setTheme({
                primary: "#C0D6DF",
                secondary: "#4F6D7A",
                tertiary: "#ebecee",
                initial: false,
            });
        } else {
            setTheme({
                primary: "#CEAE8C",
                secondary: "#2A313B",
                tertiary: "#dfd5ca",
                initial: false,
            });
        }
    }, [pathname]);

    return (
        <ThemeProvider theme={theme}>
            <div className="App">
                <GlobalStyle1 theme={theme} />

                <Nav
                    theme={theme}
                    navStatus={navStatus}
                    setNavStatus={setNavStatus}
                    pathname={pathname}
                />

                <Switch>
                    <Route exact path="/">
                        <Home theme={theme} />
                    </Route>

                    <Route path="/aboutme">
                        <AboutMe />
                    </Route>

                    <Route path="/projects">
                        <Projects />
                    </Route>
                </Switch>
            </div>
        </ThemeProvider>
    );
}

export default App;
