// Name Animation
export const nameAnim = {
    hidden: {
        opacity: 0,
        pathLength: 0,
        pathOffset: 0,
        fill: "rgba(0,0,0,0)",
    },
    show: {
        opacity: [0, 1, 1, 1],
        pathLength: [0, 1, 1, 1],
        pathOffset: [1, 0, 0, 0],
        fill: ["rgba(0,0,0,0)", "rgba(0,0,0,0)", "rgba(0,0,0,0)", "#CEAE8C"],
        transition: {
            ease: "linear",
            duration: 1.75,
            times: [0, 0.6, 0.7, 1],
        },
    },
};

// Burger Animation
export const burgerAnim = {
    hidden: {
        scale: 1,
        rotate: 0,
        opacity: 1,
        transition: { duration: 0.5, ease: "easeOut" },
    },
    show: {
        scale: 0.3,
        rotate: 270,
        opacity: 0,
        transition: { duration: 0.5, ease: "easeOut" },
    },
};

// Cross Animation
export const crossAnim = {
    hidden: {
        scale: 0.3,
        rotate: 0,
        opacity: 0,
        transition: { duration: 0.5, ease: "easeOut" },
    },
    show: {
        scale: 1,
        rotate: 270,
        opacity: 1,
        transition: { duration: 0.5, ease: "easeOut" },
    },
};

// Navbar Entrance and Leave
export const navEnter = {
    show: {
        x: 0,
        opacity: 1,
        transition: {
            duration: 0,
            staggerChildren: 0.05,
            ease: "easeOut",
            when: "beforeChildren",
        },
    },
};

export const navLeave = {
    show: {
        opacity: 0,
        x: "-120%",
        transition: {
            duration: 0,
            when: "afterChildren",
        },
    },
};

