import React from "react";

// Animations
import { motion } from "framer-motion";
import {
    modalSlideIn,
    modalSlideOut,
    modalRight,
    modalLeft,
    barUp,
    barDown,
} from "../animations";

// Styles
import styled from "styled-components";

const ProjectModal = ({ modalProject, modalToggle, setModalToggle }) => {
    // Modal Close Handler
    const modalCloseHandler = () => {
        setModalToggle(!modalToggle);
        document.body.style.overflow = "auto";
        document.querySelector(".scroll").style.overflow = "hidden";
    };

    return (
        <StyledModal
            className="scroll"
            variants={modalToggle ? modalSlideIn : modalSlideOut}
            animate="show"
            initial={false}
        >
            <BackBar
                variants={modalToggle ? barUp : barDown}
                animate="show"
                initial={false}
                onClick={modalCloseHandler}
            >
                <motion.div>back</motion.div>
            </BackBar>

            <ProjectInfo
                variants={modalToggle ? modalRight : modalLeft}
                animate="show"
                initial={false}
            >
                <ProjectImage>
                    <motion.img
                        src={modalProject.image}
                        alt={modalProject.name}
                    />
                </ProjectImage>
                <ProjectContent>
                    <ProjectTitle>
                        <motion.h1>{modalProject.name}</motion.h1>
                        <motion.a href={modalProject.link} target="_blank">
                            visit here
                        </motion.a>
                        {modalProject.github ? (
                            <motion.a
                                href={modalProject.github}
                                target="_blank"
                            >
                                source
                            </motion.a>
                        ) : (
                            ""
                        )}
                    </ProjectTitle>
                    <motion.p>{modalProject.description}</motion.p>
                    <motion.h3>SKILLS USED</motion.h3>
                    <motion.p>{modalProject.skills}</motion.p>
                </ProjectContent>
            </ProjectInfo>
        </StyledModal>
    );
};

const StyledModal = styled(motion.div)`
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    display: flex;
    transform: translateY(100vh);
    z-index: 100;
    overflow-y: auto;
`;

const BackBar = styled(motion.div)`
    width: 4rem;
    height: 100vh;
    position: sticky;
    top: 0;
    left: 0;
    background: #ffb0a6;
    display: flex;
    align-items: center;
    justify-content: center;
    transform: translateY(100%);
    transition: background 0.5s ease;
    cursor: pointer;
    z-index: 101;

    div {
        transform: rotate(-90deg);
        color: #eff2f5;
        font-family: "Lato", sans-serif;
        font-size: 1.2rem;
    }

    &:hover {
        background: #fd9183;
    }
`;

const ProjectInfo = styled(motion.div)`
    background: #f4f5f7;
    width: calc(100% - 4rem);
    min-height: 100vh;
    position: fixed;
    top: 0;
    right: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    transform: translateX(-100%);
    padding-top: 3rem;
`;

const ProjectTitle = styled(motion.div)`
    display: flex;
    position: relative;

    a {
        display: flex;
        align-items: center;
        justify-content: center;
        font-family: "Lato", sans-serif;
        width: 6.5rem;
        height: 3rem;
        border: 0.15rem rgba(255, 176, 166, 1) solid;
        background: rgba(255, 176, 166, 0);
        border-radius: 0.5rem;
        color: #ffb0a6;
        font-size: 1.2rem;
        margin-left: 1rem;
        position: relative;
        top: 4rem;
        transition: all 0.5s ease;

        &:hover {
            background: #ffc7c0;
            color: #eff2f5;
        }
    }
`;

const ProjectImage = styled(motion.div)`
    width: 70%;
    overflow: hidden;
    border-radius: 2rem;

    img {
        width: 100%;
        height: auto;
    }
`;

const ProjectContent = styled(motion.div)`
    width: 70%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    h1 {
        font-weight: bold;
        font-size: 7rem;
    }

    h3 {
        font-weight: 400;
        font-size: 5rem;
        font-family: "Lato", sans-serif;
    }

    p {
        text-align: left;
        font-size: 1.5rem;
        line-height: 1.5;
        margin-bottom: 2rem;
    }
`;

export default ProjectModal;
